import React from 'react'

import Typography from '@material-ui/core/Typography'
import { unstable_Box as Box } from '@material-ui/core/Box'
import Button from '@material-ui/core/Button'
import NewWindow from 'react-new-window'
import { TextField, Checkbox } from '@material-ui/core'
import { firebase } from '../../' // Importando firebase aqui!


class CurriculumExtraWindow extends React.Component {

    constructor(props) {
        super(props);

        this.state = {
            id: props.id,
            institution: '',
            course: '',
            company: '',
            position: '',
            valid: '',
            cid: '',
            name: props.name,
        };
    }

    componentDidMount = async () => {
        const snapshot = await firebase.firestore.collection("resumes").doc(this.props.id).get();
        const studentResume = snapshot.data();

        const {cid} = studentResume;
        if(cid) {this.setState({cid: cid})}

        const {valid} = studentResume;
        if(valid) {this.setState({valid: valid})}

        const {education} = studentResume;
        if(education && education.length) {
            if(education[0].institution) {
                this.setState({institution: education[0].institution})
            }
            if(education[0].course) {
                this.setState({course: education[0].course})
            }
        }

        const {qualification} = studentResume;
        if(qualification && qualification.length) {
            if(qualification[0].company) {
                this.setState({company: qualification[0].company})
            }
            if(qualification[0].position) {
                this.setState({position: qualification[0].position})
            }
        }

    }

    handleChange = (e) => {
        let changes = {};
        changes[e.target.name] = e.target.value;
        this.setState(changes);
    }

    handleCheckbox = (e) => {
        this.setState({valid: e.target.checked});
    }

    saveResume = async (e) => {
        e.preventDefault();

        // parse
        const qualification = [
            {company: this.state.company, position: this.state.position}
        ];

        const education = [
            {institution: this.state.institution, course: this.state.course}
        ];

        try {
            if(!this.state.id) return;
            const respose = await firebase.firestore.collection("resumes").doc(this.state.id).set({qualification, education, cid: this.state.cid, valid: this.state.valid, name: this.state.name});
            this.props.isWindowUnloaded();
        } catch (err) {
            console.log(err);
        }
    }

    render() {
        const {name, valid, position, course, institution, company, cid} = this.state;
        return(
            <NewWindow onUnload={() => this.props.isWindowUnloaded()}>
                {/* <Button color='primary' variant='contained' onClick={this.generatePdf}>Gerar PDF</Button> */}
                <div className="d-flex justify-content-center align-items-center">
                <form onSubmit={this.saveResume}>
                    <div className="container" style={{width: "500px", marginTop: "25px"}}>
                    <h1 className="d-flex align-items-center justify-content-center" style={{marginBottom: "25px"}}>Curriculo de {name}</h1>

                    <h3 className="d-flex align-items-center justify-content-center">Experincia Educacional</h3>
                        <div className="row">
                            <div className="col-12">
                                <TextField multiline={true} rows={4} style={{margin: "20px", width: "100%"}} label="Instituies Educacionais" name="institution" value={institution} onChange={this.handleChange} variant="outlined"></TextField>
                            </div>
                        </div>
                        <div className="row">
                            <div className="col-12">
                                <TextField multiline={true} rows={4} style={{margin: "20px", width: "100%"}} label="Cursos" name="course" value={course} onChange={this.handleChange} variant="outlined"></TextField>
                            </div>
                        </div>

                    <h3 className="d-flex align-items-center justify-content-center" >Experincia Profissional</h3>

                        <div className="row">
                            <div className="col-12">
                                <TextField multiline={true} rows={4} style={{margin: "20px", width: "100%"}} label="Empresas" name="company" value={company} onChange={this.handleChange} variant="outlined"></TextField>
                            </div>
                        </div>
                        <div className="row">
                            <div className="col-12">
                                <TextField multiline={true} rows={4} style={{margin: "20px", width: "100%"}} label="Cargos/Atividades" name="position" value={position} onChange={this.handleChange} variant="outlined"></TextField>
                            </div>
                        </div>

                        <h3 className="d-flex align-items-center justify-content-center" >Informaes adicionais</h3>

                        <div className="row">
                            <div className="col-12">
                                <TextField style={{margin: "20px", width: "100%"}} label="CID" name="cid" value={cid} onChange={this.handleChange} variant="outlined"></TextField>
                            </div>
                        </div>
                        <div className="row">
                            <div className="col-12 d-flex justify-content-end align-items-center" style={{marginBttom: "10px"}}>
                                <label style={{fontSize: "12pt"}}> Liberado
                            <Checkbox checked={valid}
                                      name="valid"
                                      onChange={this.handleCheckbox}
                                      value="valid"/>
                                </label>
                            </div>
                        </div>
                    </div>
                    <div class="row d-flex justify-content-end align-items-center">
                        <div className="col-12  d-flex justify-content-end align-items-center">
                        <Button style={{marginBttom: "10px"}} color="primary" variant="outlined" type="submit">Salvar</Button>
                        </div>
                    </div>
                </form>
                </div>
            </NewWindow>
        )
    }

}

export default CurriculumExtraWindow