import React from 'react'

import TableRow from '@material-ui/core/TableRow'
import TableCell from '@material-ui/core/TableCell'
import { Button } from '@material-ui/core';

import CurriculumExtraWindow from '../CurriculumExtraWindow'

class CurriculumData extends React.Component {

    constructor(props) {
        super(props)

        this.state = {
            valid: props.valid,
            id: props.id,
            name: props.name,
            techcourse: props.techcourse,
            profexperience: props.profexperience,
            cid: props.cid,
            info: false,
            position: props.position,
            company: props.company,
            institution: props.institution,
            course: props.course,
        }
    }
    /**
     * Mtodo para acionar o aparecimento da janela de informaes extras / curriculum em si.
     */
    handleOnClick = () => {
        this.setState({info: true})
    }

    /**
     * Mtodo para avisar que a janela de informaes extra foi fechada,
     * assim ela poder ser aberta novamente.
     */
    isWindowUnloaded = () => {
        this.setState({info: false});
        window.location.reload();
    }

    render() {
        return (
            <TableRow>
                <TableCell>{this.state.name}</TableCell>
                <TableCell>{this.state.techcourse}</TableCell>
                <TableCell>{this.state.profexperience}</TableCell>
                <TableCell>{this.state.cid}</TableCell>
                <TableCell>{this.state.valid}</TableCell>
                <TableCell>
                    <Button onClick={this.handleOnClick}>
                        Editar
                        {this.state.info? (
                            <CurriculumExtraWindow isWindowUnloaded={this.isWindowUnloaded} name={this.state.name} id={this.state.id}
                                    techcourse={this.state.techcourse} profexperience={this.state.profexperience}
                                        cid={this.state.cid} valid={this.state.valid ? 'liberado' : 'pendente'}
                                        />
                            ) : null}
                    </Button>
                </TableCell>
            </TableRow>
        )
    }

}

export default CurriculumData