import React from 'react'

import Container from 'components/Container'
import CurriculumManager from 'components/CurriculumManager'

/**
 * Classe Curriculum.
 * Essa classe gerenciar o estado do formulrio de busca de Curricula das pessoas.
 */
class Curriculum extends React.Component {

    constructor(props) {
        super(props)

        this.state = {
            showResult: false,
        }
    }

    /**
     * Mtodo para alternar o estado do formulrio de Busca vs Resultados.
     */
    changeFormState = () => {

        if (this.state.showResult) {
            this.setState({showResult: false})
        } else {
            this.setState({showResult: true})
        }

    }

    render() {

        const tabStruct = [
            {
                title: 'Buscar Currculos',
                component: (
                    <CurriculumManager showResult={this.state.showResult} changeFormState={this.changeFormState}/>
                ),
            },
        ]

        return (
            <div>
                <Container activeItem='Currculos' tabStruct={tabStruct}/>
            </div>
        )
    }

}

export default Curriculum